.markdown {

    font-size: 1.125rem;

    line-height: 1.75rem;

    line-height: 1.625
}

.markdown p {

    margin-top: 1.5rem;

    margin-bottom: 1.5rem
}

.markdown h1 {

    margin-top: 2rem;

    margin-bottom: 1rem;

    font-size: 2.25rem;

    line-height: 2.5rem;

    font-weight: 700
}

.markdown h2 {

    margin-top: 1.5rem;

    margin-bottom: 1rem;

    font-size: 1.875rem;

    line-height: 2.25rem;

    font-weight: 700
}

.markdown h3 {

    margin-top: 1rem;

    margin-bottom: 0.5rem;

    font-size: 1.5rem;

    line-height: 2rem;

    font-weight: 700
}

.markdown ul,
.markdown ol {

    margin-top: 1.5rem;

    margin-bottom: 1.5rem;

    list-style-position: inside;

    list-style-type: disc
}

.markdown blockquote {

    margin-top: 1.5rem;

    margin-bottom: 1.5rem;

    border-left-width: 4px;

    --tw-border-opacity: 1;

    border-color: rgb(209 213 219 / var(--tw-border-opacity, 1));

    padding-left: 1rem;

    font-style: italic
}

.markdown strong {

    font-weight: 700
}

.markdown em {

    font-style: italic
}

.markdown a {

    --tw-text-opacity: 1;

    color: rgb(37 99 235 / var(--tw-text-opacity, 1));

    text-decoration-line: underline
}

.markdown a:hover {

    --tw-text-opacity: 1;

    color: rgb(30 64 175 / var(--tw-text-opacity, 1))
}
